.table-of-content {
    a.content-title {
        @apply font-default text-[#454B57] text-[13px] leading-[22px] tracking-[0.13px] transition-all duration-200;

        &.active {
            @apply inline-flex items-center gap-2 font-medium transition-all duration-200;

            &::before {
                @apply content-[''] block w-6 h-[1.5px] bg-primary;
            }
        }
    }
}

.table-of-content.active .contentMobile {
    @apply opacity-100 z-40 max-h-[1000px] px-10 py-5;
}

.dropdownMobile svg {
    @apply duration-500
}

.table-of-content.active>.dropdownMobile svg {
    @apply rotate-180;
}

// posts content
.orely-pattern{
    &:first-child{
        @apply pb-10;
    }

    &:not(:first-child):not(:last-child){
        @apply pb-10 lg:pb-[56px];
    }

    &:last-child{
        @apply pb-0;
    }

    .wp-block-heading{
        @apply font-default text-[32px] leading-[44.8px] font-semibold text-black;
    }

    .orely-qoute-pattern {
        @apply pl-[30px];
    }

    p {
        @apply font-source-serif text-[18px] lg:text-[20px] leading-[36px] tracking-[0.8px] text-black mb-0 pb-6 lg:pb-8;
    }

    p a {
        @apply font-source-serif underline;
    }

    ol, ul {
        @apply pl-6 font-source-serif text-[18px] lg:text-[20px] leading-[36px] tracking-[0.1px] text-black pb-6 lg:pb-8;
    }

    ol li {
        @apply list-decimal;
    }

    ul li {
        @apply list-disc;
    }

    .wp-block-image{
        @apply mb-6 lg:mb-8;
    }

    img {
        @apply rounded-2xl lg:rounded-3xl;
    }

    blockquote {
        @apply pl-6 relative border-l-0 border-l-transparent mb-0 pb-6 lg:pb-8;
        &::before {
            content: '';
            @apply absolute rounded-full left-0 w-[2px] h-full bg-[#0F39A6] inset-0;
        }
    }

    p:last-child, ol:last-child, ul:last-child, figure:last-child, blockquote:last-child {
        @apply mb-0 pb-0;
    }
}

.orely-pattern .wp-block-heading + p {
    @apply pt-4;
}

.orely-pattern .wp-block-heading + .wp-block-image {
    @apply pt-8;
}

@media screen and (max-width: 1023px) {
    figure.wp-block-image.orely-image img{
        border-radius: 16px!important;
    }
}

@media screen and (min-width: 1024px) {
    figure.wp-block-image.orely-image img{
        border-radius: 20px!important;
    }
}

.orely-quote {
    @apply pb-10 lg:pb-14;
    blockquote {
        @apply pl-6 relative border-l-0 border-l-transparent;

        &::before {
            content: '';
            @apply absolute rounded-full left-0 w-[2px] h-full bg-[#0F39A6] inset-0;
        }
    }

    p:last-child {
        @apply pl-6 pt-10;
    }
}

.orely-image {
    @apply mb-0;
    img {
        @apply rounded-2xl lg:rounded-3xl;
    }
}